.contactInfo {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    background-color: #4b7d97;
    padding: 10px;
}

.contactSection {
    padding-top: 20px;
    background-color: rgba(4, 12, 24, 0.5);
    padding-bottom: 5px;
    padding-right: 5px;
    padding-left: 5px;
}

.links {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    max-width: 100%;
}

.emailLink, .githubLink, .linkedInLink {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 40px;
}

.emailForm {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: flex-end;
}

#dummyframe {
    display: none;
}

.honeypot {
    display: none;
}

input[name='name'], input[name='email'] {
    height: 30px;
    width: 300px;
}

textarea {
    height: 50px;
    width: 300px;
}

button {
    background-color: #a1afbd;
    border: none;
    border-radius: 6px;
    padding: 3px;
    box-shadow: 1px 1px 5px 1px rgba(76, 23, 90);
}

.altContactText {
    display: none;
}

@media (max-width: 1000px) {
    .contactInfo {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
    }

    .linkText {
        display: none;
    }

    .linkIcons {
        display: flex;
        gap: 30px;
    }
}

@media (max-width: 600px) {
    .mainContactText {
        display: none
    }

    .altContactText {
        display: inline;
        padding-bottom: 20px;
    }

    .contactSection {
        display: flex;
        flex-direction: column;
    }

    input[name='name'], input[name='email'] {
        height: 30px;
        width: 200px;
    }
    
    textarea {
        height: 50px;
        width: 200px;
    }

    .contactInfo {
        gap: 20px;
    }

    .submitButton {
        height: 30px;
        width: 55px;
        font-size: 15px;
    }
}

